<template>
	<view>
		<Head :tab="true" title='修改个人信息' />
		<view class="box">
			<view class="userinfo">
				<view class="My_collection">
					<text>头像</text>


					<view class="My_collection_son">
						<u-upload :fileList="fileList" @afterRead="afterRead"  name="6" 
							:maxCount="1" width="110rpx" height="110rpx">
							<img :src="userInfo.avatar" alt="">
						</u-upload>


						<u-icon name="arrow-right"></u-icon>
					</view>
				</view>
				<view class="My_collection">
					<text>昵称</text>
					<view class="My_collection_son">
						<u--input border="none" v-model="userInfo.nickname" @change='inpchange'></u--input>
						<!-- <text>{{userInfo.nickname}}</text> -->
						<u-icon name="arrow-right"></u-icon>
					</view>
				</view>

			</view>
			<view class="">
				<u-button text="保存" :disabled='disabled' @click='save' :loading='loading'
					style='width: 670rpx;height: 115rpx;background: #0F172C;border-radius: 20rpx;color: #fff;position: fixed;bottom: 30rpx;'>
				</u-button>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				userInfo: {
					nickname: '--',
				},
				disabled: true,
				num: 0,
				loading: false,
				fileList: [],
			};
		},
		onLoad() {
			this.getUser();
		},
		methods: {
			afterRead(e){
				console.log(this.fileList);
				console.log(e);
				this.userInfo.avatar = e.file.url;
				this.$my.api('/index/user/upload_avatar',{file:this.userInfo.avatar},'post','用户信息').then(res=>{
					if(res.code == 200){
						this.$my.toast('上传成功！')
					}
				})
			},
			getUser() {
				this.$my.api('/index/user/get_user_msg', {}, 'get', '用户信息').then(res => {
					this.userInfo = res.data;
					this.num = 0;
				})
			},
			inpchange(val) {
				console.log(this.num);
				if (!this.num) {
					this.num = this.num + 1;
				} else {
					this.disabled = false;

				}
			},
			save() {
				this.loading = true;
				this.$my.api('/index/user/edit_user_msg', {
					nickname: this.userInfo.nickname
				}, 'post', '修改用户信息').then(res => {
					if (res.code == 200) {
						this.$my.toast('修改成功！')
						this.getUser();
						this.loading = false;
						this.disabled = true;
					}
				})
			}
		}
	}
</script>

<style lang="less">
	.box {
		padding: 0 40rpx;
		box-sizing: border-box;
		margin-top: 48rpx;

		.userinfo {
			height: 290rpx;
			background: #FFFFFF;
			border-radius: 40rpx;
			padding: 38rpx;
			box-sizing: border-box;
			display: flex;
			flex-direction: column;
			justify-content: space-between;
		}
	}

	.My_collection {
		display: flex;
		display: flex;
		flex-direction: row;
		align-items: center;
		width: 100%;
		height: 48rpx;

		text {
			font-size: 28rpx;
			font-family: Source Han Sans CN;
			font-weight: 400;
			line-height: 56px;
			color: #333333;
			opacity: 1;
		}


		.My_collection_son {
			flex: 1;
			text-align: right;
			display: flex;
			flex-direction: row;
			align-items: center;
			justify-content: flex-end;

			.u-upload {
				display: inline-block;
				width: 110rpx;
				height: 110rpx;
				border-radius: 50%;
				flex: 0;

				.u-upload {
					width: 100%;
					height: 100%;
				}
			}

			img {
				width: 110rpx;
				height: 110rpx;
				border-radius: 50%;
			}

			view {}

			/deep/.uni-input-input {
				font-size: 24rpx;
				font-family: Source Han Sans CN;
				font-weight: 400;
				line-height: 24px;
				color: #999999;
				text-align: right;
			}

			/deep/.u-icon--right[data-v-6e20bb40] {
				float: right;
				margin-left: 29rpx;
			}
		}
	}
</style>
